.about {
	height: 100vh;
	&__wrap {
		position: relative;
		padding: 70px 0;
		height: 100vh;
		display: flex;
		align-items: center;
		&--rev {
			.about {
				&__img {
					right: auto;
					left: 650px;
					max-height: 830px; }
				&__block {
					margin-right: auto;
					margin-left: 0; } } } }
	&__img {
		position: absolute;
		right: 650px; }
	&__block {
		max-width: 600px;
		margin-left: auto; }
	&__text,
	&__list {
		color: #eaeaea;
		font-size: 17px;
		line-height: 1.55;
		font-weight: 400;
		margin-top: 20px;
		& a {
			color: #ff8562; } } }
