@keyframes rotat {
	from {
		transform: rotate(0deg); }
	to {
		transform: rotate(-360deg); } }

.proizvodstvo {
	&__wrap {
		padding-top: 70px; }
	&__first {
		display: flex;
		justify-content: space-between;
		align-items: center;
		svg {
			animation-duration: 9s;
			animation-name: rotat;
			animation-iteration-count: infinite;
			animation-timing-function: linear; } }
	&__block-635 {
		max-width: 635px;
		p {
			color: #fcf9f9;
			font-size: 24px;
			line-height: 1.55;
			font-weight: 400; } }
	&__step {
		display: flex;
		justify-content: space-between;
		svg {
			align-self: center; }
		&-item {
			display: flex;
			flex-direction: column;
			max-width: 230px; }
		&-wrap {
			padding: 50px 0; }
		&-number {
			color: #f15a25;
			font-size: 130px;
			line-height: 1.1;
			font-weight: 500; }
		&-desc {
			color: #eaeaea;
			font-size: 17px;
			line-height: 1.55;
			font-weight: 400;
			margin-top: 40px; } }
	&__desc {
	    color: #fcf9f9;
	    font-size: 17px;
	    line-height: 1.55;
	    font-weight: 400;
	    p {
	    	margin-top: 20px; }
	    &-wrap {
	    	display: flex;
	    	justify-content: space-between;
	    	align-items: flex-end;
	    	div {
	    		max-width: 720px; } } }
	&__link-product {
		position: relative;
		min-width: 170px;
		min-height: 170px;
		display: flex;
		align-items: center;
		justify-content: center;
		img {
			position: absolute;
			top: 0;
			left: 0;
			animation-duration: 9s;
			animation-name: rotat;
			animation-iteration-count: infinite;
			animation-timing-function: linear; } }
	&__images-block {
		color: #fcf9f9;
		font-size: 20px;
		line-height: 1.55;
		font-weight: 400; } }
