.form {
	visibility: hidden;
	opacity: 0;
	transition-property: opacity, visibility;
	transition-duration: .5s;
	&--active {
		visibility: visible;
		position: fixed;
		top: calc(50% - 370px);
		left: calc(50% - 280px);
		background: #9dafc7;
		height: 740px;
		width: 560px;
		padding: 40px 45px;
		opacity: 1;
		z-index: 202; }
	&__overlay {
		visibility: hidden;
		opacity: 0;
		transition-property: opacity, visibility;
		transition-duration: .5s;
		&--active {
			visibility: visible;
			position: fixed;
			top: 0;
			left: 0;
			width: 100vw;
			height: 100vh;
			background-color: rgba(0,0,0,.6);
			opacity: 1;
			transition: .5s;
			z-index: 200; } }
	&__logo {
		display: block;
		margin: auto; }
	&__text {
		margin-top: 40px;
		font-size: 20px;
		margin-bottom: 11px;
		line-height: 1.23;
		color: rgb(252, 249, 249);
		text-align: center; }
	&__form {
		display: flex;
		flex-direction: column; }
	&__input {
		color: #fcf9f9;
		border: 2px solid #f15a25;
		background-color: #9dafc7;
		margin-top: 20px;
		padding: 10px 20px;
		font-size: 16px;
		line-height: 1.33;
		width: 100%;
		&:first-child {
			margin-top: 0; }
		&::placeholder {
			font-family: 'Arial',Arial,sans-serif;
			font-size: 16px;
			line-height: 1.33;
			color: rgba(#fcf9f9, .7); } }
	&__politics {
		color: #fcf9f9;
		font-size: 15px;
		line-height: 1.55;
		font-weight: 500;
		margin-top: 20px;
		vertical-align: middle;
		input {
			margin-right: 5px; } }
	&__submit {
		color: #ffffff;
		border: 2px solid #f15a25;
		background-color: #f15a25;
		height: 54px;
		border-radius: 50px;
		text-align: center;
		font-size: 16px;
		padding-left: 60px;
		padding-right: 60px;
		font-weight: 700;
		white-space: nowrap;
		cursor: pointer;
		margin-top: 20px; } }
