.catalog {
	&__wrap {
		padding: 70px 0;
		display: flex;
		justify-content: space-between;
		align-items: flex-start; }
	&__filter {
		width: 250px;
		max-height: 830px;
		overflow-y: scroll;
		&-item {
			margin-bottom: 10px;
			a {
				color: #fcf9f9;
				font-size: 15px;
				font-weight: 300; } } }
	&__list {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		width: calc(100% - 270px);
		margin: 0 -10px; }
	&__item {
		width: calc(25% - 20px);
		margin: 10px;
		img {
			max-width: 100%; }
		p {
			font-size: 16px;
			line-height: 1.35;
			font-weight: 600;
			color: #fcf9f9;
			margin-top: 10px; } } }
